body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.background{
  background-image: url('/public/assets/images/background.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 100% 100%;
}

.row-header{
  margin: 5px;
}

.circle {
  background: lightblue;
  border-radius: 50%;
  width: 50px;
  height: 50px;
}

.p1{
  box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 4px, rgba(0, 0, 0, 0.05) 0px 0.5px 1px;
  transition: all .3s ease-in-out;
}

.p1:hover{
  box-shadow: rgba(0, 0, 0, 0) 0px 19px 43px, rgba(0, 0, 0, 0.18) 0px 4px 11px;
  background:#9575CD;
  color: #ffffff;
}

.stack-body{
  display: grid;
  grid-template-columns: auto;
  grid-gap: 10px;
  background-color: #2196F3;
  height: auto;
  width: auto;
  border: 5px solid green;
}

.black-yellow{
  background: #6D7A69;
}

.black-yellow > *{
  color: #000000;
}

.stack-body > div {
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
  height: 75px;
}

.stack-index{
  display: grid;
  grid-template-columns: auto;
  grid-gap: 10px;
  background-color: white;
  height: auto;
  width: auto;
}

.stack-index > div {
  background-color: white;
  text-align: left;
  padding: 20px 0;
  font-size: 15px;
  height: 75px;
}

.queue-body{
  display: grid;
  grid-template-rows: auto;
  grid-gap: 10px;
  background-color: green;
  height: auto;
  width: auto;
  border: 5px solid green;
}

.queue-body > div {
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
}

.queue-test{
  border: black 2px solid;
  height: 75px;
}

.queue-test > div {
  height: 75px;
}

.jumbotron{
  padding: 120px 30px 70px 30px !important;
  margin: 0px auto !important;
  background-color: #9575CD !important;
  color: floralwhite;
}

.footer{
  background-color: #D1C4E9;
  margin:0px auto;
  padding: 20px 0px 20px 0px;
}

.navbar-dark {
  background-color: #512DA8;
}